body {
  margin: 0;
  background: #104657;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.todo-container {
  width: 400px;
  margin: auto;
  margin-top: 150px;
}
.input-section {
  background: #06303d;
  border-radius: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
h2 {
  font-size: 35px;
  margin: 0;
  padding: 20px;
  color: #055a69;
}
input {
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  color: #008ba4;
  outline: none;
  width: 90%;
  border-radius: 5px;
  margin: 10px;
  border: solid 1px #00505e;
  background: #06303d;
}
input::placeholder {
  color: #008ba4;
}
ul li h4 {
  margin: 0;
  width: 80%;
}
ul {
  padding: 0;
}
ul li {
  display: flex;
  min-height: 30px;
  border-radius: 10px;
  list-style: none;
  padding: 10px;
  align-items: center;
  color: #008ba4;
  background: #06303d;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  margin-bottom: 15px;
}
ul li button {
  flex: 1;
  text-align: right;
  background: none;
  color: inherit;
  border: none;
}
ul li i {
  flex: 1;
  text-align: center;
  cursor: pointer;
  margin: 0 3px;
}
.edit-popup {
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  align-items: center;
  color: #008ba4;
  background: #06303d;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 8px;
}
.edit-popup form {
  display: flex;
  align-items: center;
}
.edit-popup form button {
  padding: 8px;
  margin: 20px;
  border-radius: 4px;
  height: 50%;
  color: #008ba4;
  cursor: pointer;
  background: #06303d;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 7px 39px, rgba(0, 0, 0, 0.22) 0px 0px 10px;
  border: none;
}
@media only screen and (max-width: 600px) {
  .todo-container {
    width: 300px;
  }
  .edit-popup {
    width: 300px;
    display: flex;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-91%, -33%);
  }
  .edit-popup form button {
    padding: 7px;
    margin: 11px;
  }
  .edit-popup form{
    width: 100%;
  }
}
